.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  width: 500px;
  height: 70vh;
  min-height: 440px;
  margin-top: 4rem;
  padding: 1rem;
  padding-top: 2rem;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  gap: 1rem;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

@media only screen and (max-width: 600px) {
  .options-container {
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
